.cursor {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    opacity: 0;
    pointer-events: none;
}

.cursor__inner {
    z-index: 100;
    fill: none;
    stroke: #fff;
    stroke-width: 2px;
}

.cursor__dot {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background: #fff;
    pointer-events: none;
    transition: transform 0.2s ease-in-out;
    transform: translate(-50% -50%);
    mix-blend-mode: exclusion;
}
